@import url(https://fonts.googleapis.com/css2?family=Aldrich&display=swap);
html {
  box-sizing: border-box;
  background-color: rgb(12, 37, 122);
}

body {
  margin: 0 0 2em 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  color: white;
  font-family: 'Aldrich', sans-serif;
}


.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

h1 {
  color: rgb(163, 163, 163);
  font-size: 4em;
  margin: 0.5em 0;
}

.hide-restart {
  display: none;
}

#restart-btn {
  border: 1px solid white;
  margin: 2em 0;
  padding: 1em 1.5em;
  border-radius: 10px;
  background-color: transparent;
  font-size: 1.3em;
}

#restart-btn:hover {
  background-color: white;
  color: rgb(12, 37, 122);
  cursor: pointer;
}

@media (max-width: 1000px) {
  #restart-btn {
    font-size: 1em;
    margin: 0 0 2em 0;
  }
}

.board {
  display: grid;
  min-height: 450px;
  min-width: 450px;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 3px;
  gap: 3px;
}

.board-element {
  border: 1px solid white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.allowed {
  background-color: green;
  cursor: pointer;
}

.not-allowed {
  cursor: not-allowed;
}

.not-allowed:hover {
  background-color: rgba(224, 5, 5, 0.5);
}

.hit,
.miss {
  font-size: 1.5em;
}

.hit {
  color: red;
}

.board-element.computer {
  cursor: crosshair;
}

.board-element.computer:hover {
  background-color: green;
}

.computer.hit,
.computer.miss {
  cursor: not-allowed;
}

.computer.hit:hover,
.computer.miss:hover {
  background-color: rgba(224, 5, 5, 0.5);
}

.board-element.ship {
  background-color: rgba(128, 128, 128, 0.5);
}

@media (max-width: 1000px) {
  .board {
    margin-bottom: 2em;
    min-height: 400px;
    min-width: 400px;
  }
}

.setup {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.axis-selection {
  border: 1px solid white;
  text-align: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  padding: 0.5em 1em;
  border-radius: 5px;
}

.axis-selection:hover {
  background-color: rgb(255, 255, 255, 0.5);
  cursor: pointer;
}
.main {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.grid-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.grid-container h2 {
  margin-top: 0;
}

@media (max-width: 1000px) {
  .main {
    -webkit-flex-direction: column;
            flex-direction: column;
    width: auto;
  }

  #enemy {
    -webkit-order: 1;
            order: 1;
  }

  #friendly {
    -webkit-order: 2;
            order: 2;
  }
}
.dialog-box {
  border: 1px solid white;
  margin-bottom: 3em;
  padding: 1.5em 0;
  border-radius: 10px;
  width: 85%;
  text-align: center;
}

.dialog-box p {
  -webkit-animation: type 3s steps(60, end);
          animation: type 3s steps(60, end);
  white-space: nowrap;
  overflow: hidden;
  margin: 0 auto;
  letter-spacing: .10em;
}

@-webkit-keyframes type {
  from { width: 0 }
  to { width: 100% }
}

@keyframes type {
  from { width: 0 }
  to { width: 100% }
}

@media (max-width: 1000px) {
  .dialog-box p {
    font-size: 0.9em;
  }

  .dialog-box {
    margin-bottom: 2em;
    padding: 1.3em 0;
  }
}

@media (max-width: 700px) {
  .dialog-box p {
    font-size: 0.8em;
  }

  .dialog-box {
    margin-bottom: 2em;
    padding: 1.3em 0;
  }
}

